var React = require("react");
import DatePicker from "react-datepicker";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";

class AddCompensationOverlay extends React.Component {
    constructor(props) {
        super(props);

        let sD = moment();
        if( this.props.offer != null ) {
            switch( this.props.compType ) {
                case "initialROE": sD = new Date( this.props.parcel.initialROEOffer.offerAmount ); break;
                case "finalROE": sD = new Date( this.props.parcel.finalROEOffer.offerAmount ); break;
                case "initialOption": sD = new Date( this.props.parcel.initialOptionOffer.offerAmount ); break;
                case "finalOption": sD = new Date( this.props.parcel.finalOptionOffer.offerAmount ); break;
                case "initialEasement": sD = new Date( this.props.parcel.initialEasementOffer.offerAmount ); break;
                case "finalEasement": sD = new Date( this.props.parcel.finalEasementOffer.offerAmount ); break;
                default: break;
            }

            sD.setMinutes( sD.getMinutes() + sD.getTimezoneOffset() );
            sD = moment(sD);
        }

        this.state = {
            startDate: sD
        }

        this.saveData = this.saveData.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(date) { this.setState({ startDate: date }); }

    saveData(action) {
        var validate = "";
        var dt = document.getElementById("add-compensation-date").value;
        var amt =  document.getElementById("add-compensation-amount").value;

        if(dt == "") { validate += "Please enter a date.  " }
        if(amt == "") { validate += "Please enter an amount.  "; }

        if(validate == "") {
            this.props.saveData(action, {
                date: dt, amount: amt,
                notes: document.getElementById("add-compensation-notes").value
            });
        } else {
            alert("There were problems with this submission:  " + validate);
        }

    }

    render() {
        let compTitle = null;
        let compAction = null;
        let editAmount = null;
        let editNotes = null;
        switch( this.props.compType ) {
            case "initialROE":
                compTitle = "Initial ROE Offer";
                compAction = "initial-roe-compensation";
                if( this.props.offer != null ) {
                    editAmount = this.props.parcel.initialROEOffer.offerAmount;
                    editNotes = this.props.parcel.initialROEOffer.offerNotes;
                }
                break;
            case "finalROE":
            compTitle = "Final ROE Offer";
            compAction = "final-roe-compensation";
            if( this.props.offer != null ) {
                editAmount = this.props.parcel.finalROEOffer.offerAmount;
                editNotes = this.props.parcel.finalROEOffer.offerNotes;
            }
            break;
            case "initialOption":
                compTitle = "Initial Option Offer";
                compAction = "initial-option-compensation";
                if( this.props.offer != null ) {
                    editAmount = this.props.parcel.initialOptionOffer.offerAmount;
                    editNotes = this.props.parcel.initialOptionOffer.offerNotes;
                }
                break;
            case "finalOption":
                compTitle = "Final Option Offer";
                compAction = "final-option-compensation";
                if( this.props.offer != null ) {
                    editAmount = this.props.parcel.finalOptionOffer.offerAmount;
                    editNotes = this.props.parcel.finalOptionOffer.offerNotes;
                }
                break;
            case "initialEasement": compTitle = "Initial Easement Offer";
                compAction = "initial-easement-compensation";
                if( this.props.offer != null ) {
                    editAmount = this.props.parcel.initialEasementOffer.offerAmount;
                    editNotes = this.props.parcel.initialEasementOffer.offerNotes;
                }
                break;
            case "finalEasement": compTitle = "Final Easement Offer";
                compAction = "final-easement-compensation";
                if( this.props.offer != null ) {
                    editAmount = this.props.parcel.finalEasementOffer.offerAmount;
                    editNotes = this.props.parcel.finalEasementOffer.offerNotes;
                }
                break;
            default: break;
        }

        return (
            <div id="add-compensation-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-dollar.png" alt="" aria-hidden="true" />
                    <h2>Add { compTitle }</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container">
                    <div className="panel-input shortest">
                        <label htmlFor="add-compensation-date">Date <span className="required">REQUIRED</span>:</label>
                        <img src="assets/images/icon-date-add.png" alt="Set Date" />
                        <DatePicker type="text" id="add-compensation-date" name="add-compensation-date" selected={ this.state.startDate } onChange={ this.handleChange } />
                    </div>

                    <div className="panel-input shortest">
                        <label htmlFor="add-compensation-amount">Amount <span className="required">REQUIRED</span>:</label>
                        <input type="text" id="add-compensation-amount" name="add-compensation-amount" defaultValue={ editAmount } />
                    </div>

                    <div className="panel-input">
                        <label htmlFor="add-compensation-notes">Notes:</label>
                        <textarea id="add-compensation-notes" name="add-compensation-notes" defaultValue={ editNotes }></textarea>
                    </div>

                    <div className="overlay-form-buttons">
                        <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) } >Cancel</a>
                        <a href="javascript:void(0);" onClick={ this.saveData.bind(null, compAction) } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                    </div>
                </div>
            </div>
        );
    }
}

module.exports = AddCompensationOverlay;
