var React = require("react");

class LeftBasemap extends React.Component {
    constructor(props) {
        super(props);

        this.launchAlert = this.launchAlert.bind(this);
    }

    launchAlert() { alert("This feature coming soon."); }

    render() {
        return (

            <div id="basemap-panel">
				<h2>BASEMAPS</h2>

				<a href="javascript:void(0);" className="basemap-link" onClick={ this.launchAlert }>
                    <img src="assets/images/basemap-sat.jpg" alt="" aria-hidden="true" />
                    Satellite
                </a>

                <a href="javascript:void(0);" className="basemap-link selected" onClick={ this.launchAlert }>
                    <img src="assets/images/basemap-sts.jpg" alt="" aria-hidden="true" />
                    Streets
                </a>

                <a href="javascript:void(0);" className="basemap-link" onClick={ this.launchAlert }>
                    <img src="assets/images/basemap-topo.jpg" alt="" aria-hidden="true" />
                    Topographic
                </a>

                <a href="javascript:void(0);" className="basemap-link" onClick={ this.launchAlert }>
                    <img src="assets/images/basemap-usgs.jpg" alt="" aria-hidden="true" />
                    USGS
                </a>
			</div>

        );
    }
}

module.exports = LeftBasemap;
