var React = require("react");
var Checkbox = require("../Display/Checkbox");
import DatePicker from "react-datepicker";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";
import StarRating from 'react-star-rating-component';
var LandownerScore = require('../Display/LandownerScore');

class AddContactLogOverlay extends React.Component {
    constructor(props) {
        super(props);

        let sD = moment();
        let owners = [];
        if( this.props.log != null ) {
            sD = new Date( this.props.log.dateAdded );
            sD.setMinutes( sD.getMinutes() + sD.getTimezoneOffset() );
            sD = moment(sD);
        }

        this.state = {
            score: this.props.log ? this.props.log.score : null,
            startDate: moment(),
            arrays: [{
                group: "owners",
                array: owners
            }, {
                group: "parcels",
                array: ( this.props.panel == "parcel" ) ? [ this.props.parcel ] : []
            }]
        }

        this.saveData = this.saveData.bind(this);
        this.setArray = this.setArray.bind(this);
        this.selectAll = this.selectAll.bind(this);
        this.selectNone = this.selectNone.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(date) { this.setState({ startDate: date }); }

    saveData(action) {
        var validate = "";
        var agt = document.getElementById("add-contact-log-agent").value;
        var ttl =  document.getElementById("add-contact-log-title").value;
        var type = document.getElementById("add-contact-log-type").value;
        var dt = document.getElementById("add-contact-log-date").value;
        var pct = document.getElementById("add-contact-log-phase").value;
        var pcls = this.state.arrays[1].array;
        const myScore = this.state.score || 2;

        if(agt == "") { validate += "Please select an agent.  " }
        if(ttl == "") { validate += "Please enter a title.  "; }
        if(type == "") { validate += "Please select a contact type.  "; }
        if(dt == "") { validate += "Please select a date.  "; }
        if(pct == "") { validate += "Please select a purpose of contact.  "; }
        if(pcls.length == 0) { validate += "Please ensure at least one parcel is selected.  "; }

        var f = document.querySelector("#fileupload");

        if(validate == "") {
            if(action == "logs") {
                this.props.saveData("logs", {
                    parcels: pcls, contacts: this.state.arrays[0].array,
                    agent: agt, title: ttl, type: type, date: dt, phase: pct,
                    notes: document.getElementById("add-contact-log-notes").value,
                    fileinput: f,
                    score: myScore
                });
            } else {
                this.props.saveData("edit-log", {
                    parcels: pcls, contacts: this.state.arrays[0].array,
                    logid: this.props.log.contactLogId,
                    agent: agt, title: ttl, type: type, date: dt, phase: pct,
                    notes: document.getElementById("add-contact-log-notes").value,
                    score: myScore
                });
            }
        } else {
            alert("There were problems with this submission:  " + validate);
        }
    }

    setArray(grp, newVal) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        var idx = arr.indexOf(newVal);

        if(idx > -1) { arr.splice( idx, 1 ); }
        else { arr.push(newVal); }
    }

    selectAll(grp) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        var list = (grp == "parcels") ?
                     this.props.parcelDetails.owners[0].ownedParcel :
                     this.props.parcelDetails.owners[0].contacts;
        var comp = (grp == "parcels") ? "parcelId" : "contactId";

        for(var i = 0; i < list.length; i++) {
            var id = list[i][comp];
            if( arr.indexOf(id) == -1 ) { arr.push(id); }
        }

        this.setState({ arr: arr });
    }

    selectNone(grp) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        arr.splice(0, arr.length);
        this.setState({ arr: [] });
    }

    _onRating(val, old, cname) {
        this.setState({ score: val });
    }

    render() {
        // console.log(this.props.log);

        let overlayTitle = "Add Contact Log";
        let editDate = "";
        let editTitle = "";
        let editType = "";
        let editAgent = "";
        let editPhase = "";
        let editNotes = "";
        let submitFunction = this.saveData.bind(this, "logs");

        let canEditRating = true;

        if(this.props.log != null) {
            canEditRating = false;
            overlayTitle = "Edit Contact Log";
            editTitle = this.props.log.title;
            editType = this.props.log.contactType;
            editAgent = this.props.log.agentName;
            editPhase = this.props.log.phase;
            editNotes = this.props.log.notes;

            for(var i = 0; i < this.props.log.contactIds.length; i++) {
                this.state.arrays[0].array.push( this.props.log.contactIds[i].contactId );
            }
            this.state.arrays[1].array = this.props.log.parcelIds;
            submitFunction = this.saveData.bind(this, "edit-log");
        }

        let agentDropdown = this.props.agents.map( a => { return (
            <option id="{a.description}" key={a.code}>{a.description}</option>
        ); });

        let purposeDropdown = this.props.purposes.map( a => { return (
            <option id="{a.code}"key={a.code}>{a.description}</option>
        ); });

        let channelDropdown = this.props.channels.map( a => { return (
            <option id="{a.code}" key={a.code}>{a.description}</option>
        ); });

        return (
            <div id="add-contact-log-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-add.png" alt="" aria-hidden="true" />
                    <h2>{ overlayTitle }</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container"> <form name="fileupload" id="fileupload" method="post" encType="multipart/form-data" >
                    <div className="same-row-inputs" style={ (this.props.log != null) ? {display: 'none'} : {} }>
                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-log-date">Date <span className="required">REQUIRED</span>:</label>
                            <img src="assets/images/icon-date-add.png" alt="Search" />
                            <DatePicker id="add-contact-log-date" name="add-contact-log-date" selected={ this.state.startDate } onChange={ this.handleChange } />
                        </div>

                        <div className="panel-input shorter">
                            <label htmlFor="add-contact-log-title">Title <span className="required">REQUIRED</span>:</label>
                            <input type="text" id="add-contact-log-title" name="add-contact-log-title" defaultValue={ editTitle } />
                        </div>
                    </div>

                    <div className="panel-input shorter" style={ (this.props.log != null) ? {display: 'none'} : {} }>
                        <label htmlFor="add-contact-log-agent">Agent <span className="required">REQUIRED</span>:</label>
                        <select id="add-contact-log-agent" name="add-contact-logo-agent" defaultValue={ editAgent } >
                            {agentDropdown}
                        </select>
                    </div>

                    <div className="same-row-inputs" style={ (this.props.log != null) ? {display: 'none'} : {} }>
                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-log-type">Contact Type <span className="required">REQUIRED</span>:</label>
                            <select id="add-contact-log-type" name="add-contact-logo-type" defaultValue={ editType } >
                                {channelDropdown}
                            </select>
                        </div>

                        <div className="panel-input shorter">
                            <label htmlFor="add-contact-log-phase">Purpose of Contact <span className="required">REQUIRED</span>:</label>
                            <select id="add-contact-log-phase" name="add-contact-logo-phase" defaultValue={ editPhase } >
                                {purposeDropdown}
                            </select>
                        </div>
                    </div>

                    <div className="panel-input">
                        <LandownerScore score={this.state.score} scores={this.props.scores} canEdit={canEditRating} onRating={ this._onRating.bind(this)} />
                        <label htmlFor="add-contact-log-notes">Notes:</label>
                        <textarea id="add-contact-log-notes" name="add-contact-log-notes" defaultValue={ editNotes } ></textarea>
                    </div>

                    <label htmlFor="myFile">File to Upload</label><br />
                    <input type="file" name="File" id="File" />
                    <br />
                    <button type="button" className="action-button esri-icon-media" onClick={this.props.snapshoot} >
                        { this.props.waitingExport ? 
                            <span style={{ fontFamily: "Open Sans Condensed", color: "blue"}}>  taking map image...</span> :
                            <span style={{ fontFamily: "Open Sans Condensed" }}> Add a Map Snapshot</span>                        
                        }
                    </button>

                    <div className="same-row-inputs">
                    <div className="half-column limit-height" style={ (this.props.log != null) ? {display: 'none'} : {} }>
                        <p>
                            <strong>Related contacts:</strong><br />
                            <a href="javascript:void(0);" className="add-contact-log-select-all-owners" onClick={ this.selectAll.bind(null, "owners") }>Select all</a><br />
                            { this.props.parcelDetails.owners[0].contacts.map(function(obj, i) {
                                let thisOwner = obj.ownerFirstName + " " + obj.ownerLastName;
                                if( obj.ownerFirstName == null ) {
                                    thisOwner = this.props.parcelDetails.owners[0].partyName
                                }

                                return(
                                    <Checkbox checked={ false }
                                        group="owners"
                                        id={ obj.contactId }
                                        key={ obj.contactId + i }
                                        setArray={ this.setArray }
                                        stateArray={ this.state.arrays[0].array }
                                        value={ thisOwner } />
                                )
                            }.bind(this)) }
                        </p>
                    </div><div className="half-column limit-height" style={ (this.props.log != null) ? {display: 'none'} : {} }>
                        <p>
                            <strong>Related parcels <span className="required">REQUIRED</span>:</strong><br />
                            <a href="javascript:void(0);" className="add-contact-log-select-all-parcels" onClick={ this.selectAll.bind(null, "parcels") }>Select all</a><br />
                            { this.props.parcelDetails.owners[0].ownedParcel.map(function(obj, i) {
                                return(
                                    <Checkbox checked={ false }
                                        group="parcels"
                                        id={ obj.parcelId }
                                        key={ obj.parcelId + i }
                                        setArray={ this.setArray }
                                        stateArray={ this.state.arrays[1].array }
                                        value={ obj.parcelId } />
                                )
                            }.bind(this)) }
                        </p>
                    </div></div>

                    <div className="overlay-form-buttons">
                        <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) }>Cancel</a>
                        <a href="javascript:void(0);" onClick={ submitFunction } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                    </div></form>
                </div>
            </div>
        );
    }
}

module.exports = AddContactLogOverlay;
