var React = require("react");

class StatsPanel extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        if( this.props.roeArray != [] ) {
            var deg, angle, currDegTotal;
            var total = 0, idx = -1, degTotal = 0;
            var amt = this.props.roeArray;
            var side = 220;
            var clrs = ["#ccc", "#0070ff", "#5f0", "#ff0", "#e60000", "#d1ff73"];

            for(var i = 0; i < amt.length; i++) { total += amt[i]; }
        }

        return (
            <div id="home-panel">
				<div id="stat-numbers">
					<div onClick={ this.props.changePanel.bind(null, "parcel", "055150000000100C" )}>
						<img src="assets/images/icon-parcels.png" alt="" aria-hidden="true" />
						<h2>TOTAL PARCELS</h2>
						<span>{ this.props.parcelTotal }</span>
					</div><div>
						<img src="assets/images/icon-owners.png" alt="" aria-hidden="true" />
						<h2>TOTAL OWNERS</h2>
						<span>{ this.props.ownerTotal }</span>
					</div>
				</div>
				<div id="stat-graphs">
                    <h3>PARCEL STATUS</h3>

                    <div id="parcel-status-graph">
                        { this.props.roeArray.map(function(val, i) {
                            if( (val / total) > .25 ) {
                                var k = 0;
                                for(var j = (val/total); j > .25; j -= .25) { k++; }

                                deg = 360 * j;
                                angle = 90 - deg;

                                currDegTotal = degTotal;
                                degTotal += deg + (k * 90);
                                idx++;

                                if(k == 1) {
                                    return (
                                        <div key={val + i + k} className="ignore">
                                            <p style={{ transform: "rotate(" + currDegTotal  + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{ transform: "rotate(" + (currDegTotal + 90) + "deg)" }}><span style={{ backgroundColor: clrs[i], transform: "skewX(" + angle + "deg) translateX(" + -((side * Math.tan( angle * (Math.PI/180) )) / 4) + "px)" }}></span></p>
                                        </div>
                                    );
                                } else if(k == 2) {
                                    return (
                                        <div key={val + i + k + 2} className="ignore">
                                            <p style={{transform: "rotate(" + currDegTotal + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{transform: "rotate(" + (currDegTotal + 90 ) + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{transform: "rotate(" + (currDegTotal + 180) + "deg)" }}><span style={{ backgroundColor: clrs[i], transform: "skewX(" + angle + "deg) translateX(" + -((side * Math.tan( angle * (Math.PI/180) )) / 4) + "px)" }}></span></p>
                                        </div>
                                    );
                                } else if(k == 3) {
                                    return (
                                        <div key={val + i + k + 3} className="ignore">
                                            <p style={{transform: "rotate(" + currDegTotal + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{transform: "rotate(" + (currDegTotal + 90 ) + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{transform: "rotate(" + (currDegTotal + 180) + "deg)"}}><span style={{ backgroundColor: clrs[i] }}></span></p>
                                            <p style={{transform: "rotate(" + (currDegTotal + 270) + "deg)" }}><span style={{ backgroundColor: clrs[i], transform: "skewX(" + angle + "deg) translateX(" + -((side * Math.tan( angle * (Math.PI/180) )) / 4) + "px)" }}></span></p>
                                        </div>
                                    );
                                }
                            } else {
                                deg = 360 * ( val / total );
                                angle = 90 - deg;

                                currDegTotal = degTotal;
                                degTotal += deg;
                                idx++;

                                return (
                                    <p key={val + i + Math.random()} style={{ transform: "rotate(" + (currDegTotal) + "deg)" }}><span style={{ backgroundColor: clrs[i], transform: "skewX(" + angle + "deg) translateX(" + -((side * Math.tan( angle * (Math.PI/180) )) / 4) + "px)" }}></span></p>
                                );
                            }
                        })}
                     </div>

                    <div className="graph-key">
                        <span id="no-activity-gk"><span></span>No Activity: { this.props.roeArray[0] }</span>
                        <span id="roe-in-progress-gk"><span></span>ROE In Progress: { this.props.roeArray[1] }</span>
                        <span id="roe-granted-gk"><span></span>Access Granted: { this.props.roeArray[5] }</span>
                        <span id="roe-obtained-gk"><span></span>ROE Obtained: { this.props.roeArray[2] }</span>
                        <span id="roe-with-conditions-gk"><span></span>ROE with Conditions: { this.props.roeArray[3] }</span>
                        <span id="no-access-gk"><span></span>No Access: { this.props.roeArray[4] }</span>
                    </div>
				</div>
			</div>
        );
    }
}

module.exports = StatsPanel;
