var React = require("react");
var StarRating = require("react-star-rating-component");
var _ = require('lodash');

const _caption = "Right of Way to be Granted Indicator (RGI)"
class LandownerScore extends React.PureComponent {

    constructor(props){
        super(props);
    }

    render() {
        var llscore = this._parseScore(this.props.score);
        var llscore_text = llscore ? llscore.description : null;

        return (
            <div>
                { (this.props.doStrong) ? 
                    <strong>{_caption}:  </strong>
                    : <span>{_caption}:  </span>
                }
                <StarRating name='loginfo-ll-score' value={this.props.score} editing={this.props.canEdit} starCount={3} onStarClick={this._changed.bind(this)} />
                {llscore_text}
            </div>
        )
    }

    _changed(val, old, cname ) {
        if ( this.props.onRating )
            this.props.onRating(val);
    }

    _parseScore(s) {
        return _.find(this.props.scores, function(sx) { return sx.code == s; });
    }
}


module.exports = LandownerScore;