var React = require("react");

class Checkbox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            arr: this.props.stateArray
        }
        this.toggleBox = this.toggleBox.bind(this);
    }

    toggleBox() {

        // TODO: code smell. 
        if( this.props.launchAlert != null) { this.props.launchAlert(this.props.stateArray.indexOf( this.props.id) <= -1) }

        this.props.setArray( this.props.group, this.props.id );
        this.setState({ arr: this.props.stateArray });
    }

    render() {
        return (
            <a href="javascript:void(0);"
                className={ (this.state.arr.indexOf( this.props.id ) > -1) ? "checkbox checked" : "checkbox" }
                onClick={ this.toggleBox }>
                    <span><img src="assets/images/icon-red-check.png" alt="checked" /></span>{ this.props.value }
            </a>
        );
    }
}

module.exports = Checkbox;
