var React = require("react");

class DocInfoOverlay extends React.Component {
    constructor(props) {
        super(props);

        this.formatDate = this.formatDate.bind(this);
        this.getDownloadLink = this.getDownloadLink.bind(this);
    }

    formatDate(dt) {
        var date = "";
        var dateToFix = dt.split("T")[0].split("-");
        switch(dateToFix[1]) {
            case "01": date += "Jan"; break; case "02": date += "Feb"; break; case "03": date += "Mar"; break;
            case "04": date += "Apr"; break; case "05": date += "May"; break; case "06": date += "Jun"; break;
            case "07": date += "Jul"; break; case "08": date += "Aug"; break; case "09": date += "Sep"; break;
            case "10": date += "Oct"; break; case "11": date += "Nov"; break; case "12": date += "Dec"; break;
            default: break;
        }
        if( dateToFix[2].charAt[0] == "0" ) { date += " " + dateToFix[2].charAt[1]; }
        else { date += " " + dateToFix[2]; }
        date += ", " + dateToFix[0];
        return date;
    }

    getDownloadLink(id) {
        return this.props.data.baseUrl() + "/documents/" + id;
    }

    render() {
        let list = this.props.doc;
        console.log(list);

        return (
            <div id="asset-overview-overlay">

                <div className="panel-header">
                    <img src="assets/images/icon-doc-wh.png" alt="" aria-hidden="true" />
                    <h2>Document Detail</h2>

                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>

                <div className="overlay-container">
                    <div className="oc-header">
                        <h3>{ list.title }</h3>
                    </div>

                    <p><strong>Document Type:</strong> { list.documentType }</p>

                    <div className="date-row">
                        <p><strong>Date Sent:</strong> { (list.sentDate != null) ? this.formatDate( list.sentDate ) : "" }</p>
                        <p><strong>Tracking Number:</strong> { (list.rowmTrackingNumber != null) ? list.rowmTrackingNumber : "" }</p>
                        <p><strong>Date Delivered:</strong> { (list.deliveredDate != null) ? this.formatDate( list.deliveredDate ) : "" }</p>
                    </div>

                    <div className="date-row">
                        <p><strong>Date Received:</strong> { (list.receivedDate != null) ? this.formatDate( list.receivedDate ) : "" }</p>
                        <p><strong>Date Signed:</strong> { (list.signedDate != null) ? this.formatDate( list.signedDate ) : "" }</p>
                        <p><strong>Date Recorded:</strong> { (list.dateRecorded != null) ? this.formatDate( list.dateRecorded ) : "" }</p>
                    </div>

                    <div className="date-row">
                        <p><strong>Check No:</strong> { (list.checkNo != null) ? list.checkNo : "" }</p>
                    </div>

                    <p className="document-links"><a href={ this.getDownloadLink( list.documentId ) } target="_blank" download >Download</a></p>
                </div>
            </div>
        );
    }
}

module.exports = DocInfoOverlay;
