var React = require("react");

class OwnerParcelLists extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="owner-parcel-list">
                <p>
                    <strong>{ this.props.listTitle }</strong><br />

                    { this.props.list.map(function(obj, i) {

                        if(this.props.parcels != null) {
                            return (
                                <a href="javascript:void(0);"
                                    key={obj + i}
                                    onClick={ this.props.changePanel.bind(null, "parcel", obj.parcelId) } >
                                        {obj.parcelId}
                                </a>
                            )
                        } else {
                            return (
                                <a href="javascript:void(0);"
                                    key={obj + i}
                                    onClick={ this.props.changePanel.bind(null, "owner", obj) } >
                                        {thisOwner.firstName} {thisOwner.lastName}
                                </a>
                            )
                        }

                    }.bind(this)) }
                </p>
            </div>
        );
    }
}

module.exports = OwnerParcelLists;
