var React = require("react");

class EditROEStatus extends React.Component {
    constructor(props) {
        super(props);

        this.saveData = this.saveData.bind(this);
    }

    saveData() {
        var val = document.getElementById("edit-roe-status").value.split(" ").join("_");
        if(val == "ROE_Obtained_with_Conditions") { val = "ROE_with_Conditions"; }

        this.props.saveData("edit-roe-status", { status: val });
    }

    render() {
        let editStatus = (this.props.status != null) ? this.props.status : "";

        return (
            <div id="edit-roe-status-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-add.png" alt="" aria-hidden="true" />
                    <h2>Edit ROE Status</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container">

                    <div className="panel-input">
                        <label htmlFor="edit-roe-status">ROE Status:</label>
                        <select id="edit-roe-status" name="edit-roe-status" defaultValue={ editStatus } >
                            <option id="ROE Obtained">ROE Obtained</option>
                            <option id="ROE Obtained with Conditions">ROE Obtained with Conditions</option>
                            <option id="Access Granted">Access Granted</option>
                            <option id="No Access">No Access</option>
                        </select>
                    </div>

                    <div className="overlay-form-buttons">
                        <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) }>Cancel</a>
                        <a href="javascript:void(0);" onClick={ this.saveData.bind(null, "edit-roe-status") } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                    </div>

                </div>
            </div>
        );
    }
}

module.exports = EditROEStatus;
