var React = require("react");
var Checkbox = require("../Display/Checkbox");
var LinedList = require("../Display/LinedList");
var OwnerParcelLists = require("../Display/OwnerParcelLists");

class OwnerPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            arrays: [{
                group: "options",
                array: []
            }]
        }

        this.setArray = this.setArray.bind(this);
        this.outputEmailLink = this.outputEmailLink.bind(this);
        this.launchAlert = this.launchAlert.bind(this);
        this.launchAlert = this.launchAlert.bind(this);
    }

    launchAlert(display) { 
        if ( display ) { 
            let pids = this.props.parcel.owners[0].ownedParcel.map( function(px) { return px.parcelId; });
            this.props.viewParcels(pids); 
        } else {
            this.props.viewParcels();
        }
    }

    outputEmailLink(eml) { return "mailto:" + eml; }

    setArray(grp, newVal) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        var idx = arr.indexOf(newVal);

        if(idx > -1) { arr.splice( idx, 1 ); }
        else { arr.push(newVal); }
    }

    render() {
        console.log( this.props.parcel );

        let owner = this.props.parcel.owners[0];

        let sortedContacts = [];
        let tempContacts = [];
        for(var i = 0; i < owner.contacts.length; i++ ) {
            tempContacts[i] = { };
            for(var j = 0; j < Object.keys(owner.contacts[i]).length; j++) {
                tempContacts[i][ Object.keys(owner.contacts[i])[j] ] = owner.contacts[i][ Object.keys(owner.contacts[i])[j] ];
            }
        }

        for(var i = 0; i < tempContacts.length; i++) {
            if( tempContacts[i].isPrimary ) {
                sortedContacts = tempContacts.splice(i, 1);
            }
        }

        sortedContacts = sortedContacts.concat(tempContacts);

        return (
            <div id="owner-panel">
				<div className="panel-header">
					<img src="assets/images/icon-owner-wh.png" alt="" aria-hidden="true" />
					<h2>{ owner.partyName }</h2>
					<span className="panel-close" onClick={ this.props.changePanel.bind(null, "stats", null) }>X</span>
				</div>

				<div className="right-panel-content">
					<h3>CONTACT INFORMATION</h3>

                    { sortedContacts.map(function(obj, i) {
                        let name = obj.ownerLastName;
                        if( obj.ownerFirstName != null ) { name = obj.ownerFirstName + " " + obj.ownerLastName  }

                        return(
                            <div className="contact-info" key={obj.ownerLastName + i}>
                                <div className="half-column">
                                    <p>
                                        {name} <a href="javascript:void(0);" onClick={ this.props.launchOverlay.bind(null, "edit-contact", obj.contactId ) }><img src="assets/images/icon-pencil-wh.png" alt="" aria-hidden="true" /></a>
                                    </p>
            						<p>
            							<strong>Mailing Address:</strong><br />
            							{ obj.ownerStreetAddress }<br />
            							{ obj.ownerCity }, { obj.ownerState } { obj.ownerZIP }
            						</p>
            						<p>
            							<strong>Email:</strong> <a href={ this.outputEmailLink( obj.ownerEmail ) }>{ obj.ownerEmail }</a>
            						</p>
                                    <p>
                                        <strong>Relationship:</strong> { obj.relations }
                                    </p>
            					</div><div className="half-column">
            						<p>
            							<strong>Home phone:</strong> { obj.ownerHomePhone }<br />
            						</p>
            						<p>
            							<strong>Cell phone:</strong> { obj.ownerCellPhone }<br />
            						</p>
            						<p>
            							<strong>Work phone:</strong> { obj.ownerWorkPhone }<br />
            						</p>
                                </div>
                            </div>
                        )
                    }.bind(this)) }

                    <a href="javascript:void(0);" className="red-button" onClick={ this.props.launchOverlay.bind(null, "add-contact") }><img src="assets/images/icon-add.png" alt="" aria-hidden="true" />Add new contact</a>

					<h3>AGENT LOGS</h3>
					<LinedList
                        launchOverlay={ this.props.launchOverlay }
                        logs={ this.props.parcel.contactsLog }
                        list={ this.props.parcel.contactsLog } />

					<a href="javascript:void(0);" className="red-button" onClick={ this.props.launchOverlay.bind(null, "add-contact-log", null) }><img src="assets/images/icon-add.png" alt="" aria-hidden="true" />Add log</a>

                    <h3>PARCELS</h3>
                    <div id="parcels-view-on-map">
						<img src="assets/images/icon-globe.png" alt="" aria-hidden="true" />
						<strong>View on map:</strong>
                        <Checkbox
                            group="options"
                            id="view-on-map"
                            launchAlert={ this.launchAlert }
                            setArray={ this.setArray }
                            stateArray={ this.state.arrays[0].array }
                            value="" />
					</div>

                    <OwnerParcelLists
                        changePanel={ this.props.changePanel }
                        currentParcel={ this.props.parcel.owners[0].parcelId }
                        listTitle=""
                        list={ this.props.parcel.owners[0].ownedParcel }
                        parcels={ this.props.parcel.owners[0].ownedParcel } />
				</div>
			</div>
        );
    }
}

module.exports = OwnerPanel;
