var React = require("react");
var Checkbox = require("../Display/Checkbox.js");
import DatePicker from "react-datepicker";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";

class AddDocumentOverlay extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            dateSent: "",
            dateDelivered: "",
            dateReceived: "",
            dateSigned: "",
            dateRecorded: "",
            trackingNumber: "",
            checkNo: "",

            arrays: [{
                group: "owners",
                array: []
            }, {
                group: "parcels",
                array: [ this.props.parcel ]
            }]
        }

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleDateSent = this.handleDateSent.bind(this);
        this.handleDateDelivered = this.handleDateDelivered.bind(this);
        this.handleDateReceived = this.handleDateReceived.bind(this);
        this.handleDateSigned = this.handleDateSigned.bind(this);
        this.handleDateRecorded = this.handleDateRecorded.bind(this);

        this.setArray = this.setArray.bind(this);
        this.selectAll = this.selectAll.bind(this);
    }

    handleDateSent(date) { this.setState({dateSent: date }); }
    handleDateDelivered(date) { this.setState({ dateDelivered: date }); }
    handleDateReceived(date) { this.setState({ dateReceived: date }); }
    handleDateSigned(date) { this.setState({ dateSigned: date }); }
    handleDateRecorded(date) {this.setState({ dateRecorded: date })}

    handleSubmit() {
        var validate = "";
        var pcls = this.state.arrays[1].array;

        console.log(pcls);

        if(document.getElementById("documentTitle").value == "") { validate += "Please enter a title.  " }
        if(document.getElementById("documentType").value == "") { validate += "Please select a type.  "; }
        if(document.getElementById("file").value == "") { validate += "Please select a file.  "; }

        if(validate == "") {
            var app = this;
            var xmlHR= new XMLHttpRequest();
            xmlHR.onload = function(e) {
                if (xmlHR.status === 200) {
                    console.log("IGISGHUISDH");
                    console.log(e.currentTarget);

                    var id = e.currentTarget.response.split('"documentId":"')[1].split('"')[0];

                    app.props.saveData("document-metadata", {
                        docId: id,
                        trackingNo: (document.getElementById("add-document-tracking-number")) ? document.getElementById("add-document-tracking-number").value : null,
                        dateSent: (document.getElementById("add-document-date-sent")) ? document.getElementById("add-document-date-sent").value : null,
                        dateDelivered: (document.getElementById("add-document-date-delivered")) ? document.getElementById("add-document-date-delivered").value : null,
                        dateReceived: (document.getElementById("add-document-date-received")) ? document.getElementById("add-document-date-received").value : null,
                        dateSigned: (document.getElementById("add-document-date-signed")) ? document.getElementById("add-document-date-signed").value : null,
                        dateRecorded: (document.getElementById("add-document-date-recorded")) ? document.getElementById("add-document-date-recorded").value : null,
                        checkNo: (document.getElementById("add-document-check-no")) ? document.getElementById("add-document-check-no").value : null
                    });
                } else { console.log("Ajax error"); }
            };

            var fData = new FormData( document.forms.namedItem("fileupload") );
            for(var i = 0; i < pcls.length; i++) {
                fData.append("ParcelIds", pcls[i]);
            }

            xmlHR.open("POST", this.props.data.baseUrl() + "/addDocument", true);
            xmlHR.send( fData );
        } else {
            alert("There were problems with this submission:  " + validate);
        }
    }

    setArray(grp, newVal) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        var idx = arr.indexOf(newVal);

        if(idx > -1) { arr.splice( idx, 1 ); }
        else { arr.push(newVal); }
    }

    selectAll(grp) {
        var arr = (grp == "parcels") ? this.state.arrays[1].array : this.state.arrays[0].array;
        var list = (grp == "parcels") ?
                     this.props.parcelDetails.owners[0].ownedParcel :
                     this.props.parcelDetails.owners[0].contacts;
        var comp = (grp == "parcels") ? "parcelId" : "contactId";

        for(var i = 0; i < list.length; i++) {
            var id = list[i][comp];
            if( arr.indexOf(id) == -1 ) { arr.push(id); }
        }

        this.setState({ arr: arr });
    }

    render() {
        let editTitle = "";
        if(this.props.doc != null) {
            editTitle = this.props.doc.title;
        }

        let docts = this.props.docTypes.map( (dt,idx) => {
            return <option defaultValue="{dt.docTypeName}" key={idx}>{dt.docTypeName}</option>
        });

        return (
            <div id="add-document-overlay" className="wider">
                <div className="panel-header">
                    <img src="assets/images/icon-add.png" alt="" aria-hidden="true" />
                    <h2>Add a document</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>

                <div className="overlay-container">
                    <form name="fileupload" method="post" encType="multipart/form-data">

                        <div className="panel-input shorter">
                            <label htmlFor="documentType">Document Type <span className="required">REQUIRED</span>:</label>
                            <select id="documentType" name="documentType">
                            {docts}
                            </select>
                        </div>

                        <div className="panel-input shorter">
                            <label htmlFor="documentTitle">Title <span className="required">REQUIRED</span>:</label>
                            <input type="text" id="documentTitle" name="documentTitle" defaultValue={ editTitle } />
                        </div>

                        <label htmlFor="file">File to Upload <span className="required">REQUIRED</span></label><br />
                        <input type="file" name="file" id="file" />

                        <div className="same-row-inputs">
                            <div className="panel-input shortest">
                                <label htmlFor="add-document-date-set">Date Sent:</label>
                                <img src="assets/images/icon-date-add.png" alt="Search" />
                                <DatePicker type="text" id="add-document-date-sent" name="add-document-date-sent" selected={ this.state.dateSent } onChange={ this.handleDateSent } />
                            </div>

                            <div className="panel-input shortest">
                                <label htmlFor="add-document-date-delivered">Date Delivered:</label>
                                <img src="assets/images/icon-date-add.png" alt="Search" />
                                <DatePicker type="text" id="add-document-date-delivered" name="add-document-date-delivered" selected={ this.state.dateDelivered } onChange={ this.handleDateDelivered } />
                            </div>

                            <div className="panel-input shortest">
                                <label htmlFor="add-document-tracking-number">Tracking Number:</label>
                                <input type="text" id="add-document-tracking-number" name="add-document-tracking-number" defaultValue="" />
                            </div>
                        </div>

                        <div className="same-row-inputs">
                            <div className="panel-input shortest">
                                <label htmlFor="add-document-date-received">Date Received:</label>
                                <img src="assets/images/icon-date-add.png" alt="Search" />
                                <DatePicker type="text" id="add-document-date-received" name="add-document-date-received" selected={ this.state.dateReceived } onChange={ this.handleDateReceived } />
                            </div>

                            <div className="panel-input shortest">
                                <label htmlFor="add-document-date-signed">Date Signed:</label>
                                <img src="assets/images/icon-date-add.png" alt="Search" />
                                <DatePicker type="text" id="add-document-date-signed" name="add-document-date-signed" selected={ this.state.dateSigned } onChange={ this.handleDateSigned } />
                            </div>

                            <div className="panel-input shortest">
                                <label htmlFor="add-document-check-no">Check No:</label>
                                <input type="text" id="add-document-check-no" name="add-document-check-no" defaultValue="" />
                            </div>
                        </div>

                        <div className="same-row-inputs">
                            <div className="panel-input shortest">
                                <label htmlFor="add-document-date-recorded">Date Recorded:</label>
                                <img src="assets/images/icon-date-add.png" alt="Search" />
                                <DatePicker type="text" id="add-document-date-recorded" name="add-document-date-recorded" selected={ this.state.dateRecorded } onChange={ this.handleDateRecorded } />
                            </div>
                        </div>

                        <input type="hidden" id="agentName" name="agentName" defaultValue="Erin Begier" />

                        <p>
                            <strong>Related parcels <span className="required">REQUIRED</span>:</strong><br />
                            <a href="javascript:void(0);" className="add-document-select-all-parcels" onClick={ this.selectAll.bind(null, "parcels") }>Select all</a><br />
                            { this.props.parcelDetails.owners[0].ownedParcel.map(function(obj, i) {
                                return(
                                    <Checkbox checked={ false }
                                        group="parcels"
                                        id={ obj.parcelId }
                                        key={ obj.parcelId + i }
                                        setArray={ this.setArray }
                                        stateArray={ this.state.arrays[1].array }
                                        value={ obj.parcelId } />
                                )
                            }.bind(this)) }
                        </p>

                        <div className="overlay-form-buttons">
                            <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) }>Cancel</a>
                            <a href="javascript:void(0);" onClick={ this.handleSubmit.bind(null) } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                        </div>

                    </form>
                </div>
            </div>
        );
    }
}

module.exports = AddDocumentOverlay;
