var React = require("react");

class LeftSearch extends React.Component {
    constructor(props) {
        super(props);

        this.launchAlert = this.launchAlert.bind(this);
    }

    launchAlert() { alert("This feature coming soon."); }

    render() {
        return (

            <div id="search-panel">
				<h2>SEARCH</h2>

				<div className="panel-input" onClick={ this.launchAlert }>
					<label htmlFor="search-by-owner">Search by owner</label>
					<input type="text" id="search-by-owner" name="search-by-owner" />
					<a href="javascript:void(0);"><img src="assets/images/icon-sm-search.png" alt="Search" /></a>
				</div>

				<div className="panel-input" onClick={ this.launchAlert }>
					<label htmlFor="search-by-parcel-id">Search by parcel ID</label>
					<input type="text" id="search-by-parcel-id" name="search-by-parcel-id" />
					<a href="javascript:void(0);"><img src="assets/images/icon-sm-search.png" alt="Search" /></a>
				</div>

				<div className="panel-input" onClick={ this.launchAlert }>
					<label htmlFor="filter-by-status">Filter by status</label>
					<select id="filter-by-status" name="filter-by-status">
						<option id="owner-contacted">Owner contacted</option>
						<option id="roe-obtained">ROE obtained</option>
						<option id="offer-made">Offer made</option>
						<option id="easement-signed">Easement signed</option>
						<option id="compensation-received">Compensation received</option>
					</select>
				</div>
			</div>

        );
    }
}

module.exports = LeftSearch;
