var React = require("react");
var LinedList = require("../Display/LinedList");

class RelatedOwnersOverlay extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div id="related-owners-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-owners-wh.png" alt="" aria-hidden="true" />
                    <h2>Related Contacts</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container">
                <LinedList
                    changePanel={ this.props.changePanel }
                    list={ this.props.parcel.owners[0].contacts }
                    owners={ this.props.parcel.owners[0] } />

                    <a href="javascript:void(0);" className="red-button" onClick={ this.props.launchOverlay.bind(null, "add-contact") }><img src="assets/images/icon-add.png" alt="" aria-hidden="true" />Add related contact</a>
                </div>
            </div>
        );
    }
}

module.exports = RelatedOwnersOverlay;
