var React = require("react");

class LinedList extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="option-list">
                { this.props.list.map(function(obj, i) {

                    ////////////  LOGS  ////////////////////
                    if(this.props.logs != null) {
                        var date = "", dateToFix = obj.dateAdded.split("T")[0].split("-");
                        switch(dateToFix[1]) {
                            case "01": date += "Jan"; break; case "02": date += "Feb"; break; case "03": date += "Mar"; break;
                            case "04": date += "Apr"; break; case "05": date += "May"; break; case "06": date += "Jun"; break;
                            case "07": date += "Jul"; break; case "08": date += "Aug"; break; case "09": date += "Sep"; break;
                            case "10": date += "Oct"; break; case "11": date += "Nov"; break; case "12": date += "Dec"; break;
                            default: break;
                        }
                        if(dateToFix[2].charAt[0] == "0") { date += " " + dateToFix[2].charAt[1]; }
                        else { date += " " + dateToFix[2]; }
                        date += ", " + dateToFix[0];

                        return (
                            <a href="javascript:void(0);"
                                key={obj + i}
                                onClick={ this.props.launchOverlay.bind(null, "contact-log", obj) } >
                                    <img src="assets/images/icon-contact.png" alt="" aria-hidden="true" />
                                    { obj.title }
                                    <span>{ date }</span>
                            </a>
                        )

                    ///////////  OWNERS  //////////////////
                    } else if(this.props.owners != null) {
                        let thisOwner = obj.ownerFirstName + " " + obj.ownerLastName;
                        if( obj.ownerFirstName == null ) { thisOwner = this.props.owners.partyName }

                        let relationship = null;
                        if( this.props.showRelationship != null ) {
                            for(var idx = 0; idx < this.props.bridge.length; idx++) {
                                if( ( this.props.bridge[idx]["owner"] == obj ) &&
                                    ( this.props.bridge[idx]["primaryOwner"] == this.props.primaryOwnerID ) ) {
                                        relationship = this.props.bridge[idx]["relationship"];
                                    }
                            }
                        }

                        return (
                            <a href="javascript:void(0);"
                                key={obj + i}
                                onClick={ this.props.changePanel.bind(null, "owner", obj) } >
                                    <img src="assets/images/icon-owner.png" alt="" aria-hidden="true" />
                                    { thisOwner }
                                    <span>{ relationship }</span>
                            </a>
                        )

                    //////////  DOCUMENTS  ///////////////
                    } else {
                        return (
                            <a href="javascript:void(0);"
                                key={obj + i}
                                onClick={ this.props.launchOverlay.bind(null, "document-detail", obj) } >
                                    <img src="assets/images/icon-doc.png" alt="" aria-hidden="true" />
                                    { obj.documentTitle }
                                    <span></span>
                            </a>
                        )
                    }

                }.bind(this)) }
            </div>

        );
    }
}

module.exports = LinedList;
