var React = require("react");

class LeftLegend extends React.Component {
    constructor(props) {
        super(props);

        this.launchAlert = this.launchAlert.bind(this);
    }

    launchAlert() { alert("This feature coming soon."); }

    render() {
        return (

            <div id="legend-panel">
                <h2>LEGEND</h2>

				<a href="javascript:void(0);" className="checkbox" onClick={ this.launchAlert }><span></span>Transmission alignment</a>
                <a href="javascript:void(0);" className="checkbox" onClick={ this.launchAlert }><span></span>Parcels</a>
			</div>

        );
    }
}

module.exports = LeftLegend;
