var React = require("react");
var Checkbox = require("../Display/Checkbox");

class AddContact extends React.Component {
    constructor(props) {
        super(props);

        this.saveData = this.saveData.bind(this);
    }

    saveData(action) {
        var validate = "";

        if( document.getElementById("add-contact-isPrimary").checked ) {
            var contacts = this.props.parcel.owners[0].contacts;
            for(var i = 0; i < contacts.length; i++) {
                if( contacts[i].isPrimary == true ) {
                    if( this.props.contact != null ) {
                        if( this.props.contact != contacts[i].contactId ) {
                            validate += "There is already another primary owner in the database.  "
                        }
                    } else {
                        validate += "There is already another primary owner in the database.  "
                    }
                }
            }
        }


        var fnm = document.getElementById("add-contact-first-name").value;
        var lnm =  document.getElementById("add-contact-last-name").value;

        if(fnm == "" || lnm == "") { validate += "Please enter a full name.  " }

        if(validate == "") {
            this.props.saveData(action, {
                firstName: fnm, lastName: lnm,
                address: document.getElementById("add-contact-address").value,
                city: document.getElementById("add-contact-city").value,
                state: document.getElementById("add-contact-state").value,
                zip: document.getElementById("add-contact-zip").value,
                email: document.getElementById("add-contact-email").value,
                homePhone: document.getElementById("add-contact-home-phone").value,
                workPhone: document.getElementById("add-contact-work-phone").value,
                cellPhone: document.getElementById("add-contact-cell-phone").value,
                isPrimary: document.getElementById("add-contact-isPrimary").checked,
                relationship: document.getElementById("add-contact-relationship").value
            });
        } else {
            alert("There were problems with this submission:  " + validate);
        }
    }

    render() {
        let overlayTitle = "Add Contact";
        let editFirstName = ""; let editLastName = ""; let editAddress = ""; let editCity = "";
        let editState = ""; let editZip = ""; let editEmail = ""; let editHomePhone = "";
        let editWorkPhone = ""; let editCellPhone = ""; let editIsPrimary = ""; let editRelationship = "Self";
        let submitFunction = this.saveData.bind(this, "contact");

        if(this.props.contact != null) {
            let idx = null;
            let contacts = this.props.parcel.owners[0].contacts;
            for(var i = 0; i < contacts.length; i++) {
                if(contacts[i].contactId == this.props.contact) { idx = i; break; }
            }

            if( contacts[idx].isPrimary ) { editIsPrimary = "checked";  }

            overlayTitle = "Edit Contact";
            editFirstName = contacts[idx].ownerFirstName;
            editLastName = contacts[idx].ownerLastName;
            editAddress = contacts[idx].ownerStreetAddress;
            editCity = contacts[idx].ownerCity;
            editState = contacts[idx].ownerState;
            editZip = contacts[idx].ownerZIP;
            editEmail = contacts[idx].ownerEmail;
            editHomePhone = contacts[idx].ownerHomePhone;
            editWorkPhone = contacts[idx].ownerWorkPhone;
            editCellPhone = contacts[idx].ownerCellPhone;
            editRelationship = contacts[idx].relations;
            submitFunction = this.saveData.bind(this, "edit-contact");
        }

        let relationDropdown = this.props.relationships.map( a => { return ( 
            <option id="{a.code}" key={a.key} >{a.description}</option>
        );});

        return (
            <div id="add-contact-log-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-add.png" alt="" aria-hidden="true" />
                    <h2>{ overlayTitle }</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container">
                    <div className="same-row-inputs">
                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-first-name">First Name <span className="required">REQUIRED</span>:</label>
                            <input type="text" id="add-contact-first-name" name="add-contact-first-name" defaultValue={ editFirstName } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-last-name">Last Name <span className="required">REQUIRED</span>:</label>
                            <input type="text" id="add-contact-last-name" name="add-contact-last-name" defaultValue={ editLastName } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-isPrimary">Is Primary Contact:</label>
                            <input type="checkbox" id="add-contact-isPrimary" name="add-contact-isPrimary" defaultChecked={ editIsPrimary } />
                        </div>
                    </div>

                    <div className="panel-input shorter">
                        <label htmlFor="add-contact-relationship">Relationship:</label>
                        <select id="add-contact-relationship" name="add-contact-relationship" defaultValue={ editRelationship } >
                            {relationDropdown}
                            {/*<option id="Self">Self</option>
                            <option id="Spouse">Spouse</option>
                            <option id="Child">Child</option>
                            <option id="Tenant">Tenant</option>
                            <option id="Attorney">Attorney</option>
        <option id="Other">Other</option>*/}
                        </select>
                    </div>

                    <div className="panel-input shorter">
                        <label htmlFor="add-contact-address">Street Address:</label>
                        <input type="text" id="add-contact-address" name="add-contact-address" defaultValue={ editAddress } />
                    </div>

                    <div className="same-row-inputs">
                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-city">City:</label>
                            <input type="text" id="add-contact-city" name="add-contact-city" defaultValue={ editCity } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-state">State:</label>
                            <input type="text" id="add-contact-state" name="add-contact-state" defaultValue={ editState } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-zip">ZIP Code:</label>
                            <input type="text" id="add-contact-zip" name="add-contact-zip" defaultValue={ editZip } />
                        </div>
                    </div>

                    <div className="panel-input shorter">
                        <label htmlFor="add-contact-email">Email Address:</label>
                        <input type="text" id="add-contact-email" name="add-contact-email" defaultValue={ editEmail } />
                    </div>

                    <div className="same-row-inputs">
                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-home-phone">Home Phone:</label>
                            <input type="text" id="add-contact-home-phone" name="add-contact-home-phone" defaultValue={ editHomePhone } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-work-phone">Work Phone:</label>
                            <input type="text" id="add-contact-work-phone" name="add-contact-work-phone" defaultValue={ editWorkPhone } />
                        </div>

                        <div className="panel-input shortest">
                            <label htmlFor="add-contact-cell-phone">Cell Phone:</label>
                            <input type="text" id="add-contact-cell-phone" name="add-contact-cell-phone" defaultValue={ editCellPhone } />
                        </div>
                    </div>

                    <div className="overlay-form-buttons">
                        <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) }>Cancel</a>
                        <a href="javascript:void(0);" onClick={ submitFunction } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                    </div>
                </div>
            </div>
        );
    }
}

module.exports = AddContact;
