import * as React from 'react';
import EsriLoader from 'esri-loader-react';
import { loadModules } from 'esri-loader';
import { addListener } from 'cluster';

// require("https://js.arcgis.com/4.8/esri/css/main.css")
interface MapAreaProps {
  mapWidth: number;
  parcelUrl: string;
  refUrl: string;
  ident( pid: string );
  doSketch: boolean;
  callback( 
    pvm: any, // print
    grahics: any, // graphics
    line: any, // comment lines
    point: any, // comment points
    highlighter: any, // start markup
    redraw: () => void
  );
}

const _AGS = "https://gis05.hdrgateway.com/arcgis/rest/services";
const _PAR = "California/B2H_ROW_Parcels_FS";
const _MAP = "California/B2H_ROW_MapService"


class MapArea extends React.Component<MapAreaProps,any> {

  constructor(private props){
    super(props);
  }

  private vi : any;
  private parcelLay: any;
  private highlighted: any;
  private mapContainer: any;

  private editGraphic: any // sketching
  private svm: any; // Sketch
  private glay: any; // graphics
  private lineLay: any;
  private pointLay: any;
  private tmplate = {
    title: 'B2H Markup',
    overwriteActions: true,
    content: '<textarea rows="4" cols="80" id="markupNotes" >{notes}</textarea>',
    actions: [{ id:"mapn-save", title:"Apply", className:"esri-icon-check-mark"}, { id:"mapn-clear", title:"Reset", className:"esri-icon-trash"}]
  };

    _makeMap = () => {

      if ( this.vi ) return;
      const opts = {
        url: "https://js.arcgis.com/4.9/",
        dojoConfig: {
          has: { "esri-featurelayer-webgl": 1 }
        }
      }
    

      loadModules([ "esri/Map", "esri/views/MapView", "esri/layers/FeatureLayer", "esri/layers/MapImageLayer", "esri/widgets/BasemapToggle", "esri/widgets/Expand", "esri/widgets/Legend", "esri/widgets/Search", "esri/widgets/LayerList", "esri/Graphic", "esri/layers/GraphicsLayer", "esri/widgets/Sketch/SketchViewModel", "esri/widgets/Print", "esri/widgets/Print/PrintViewModel", "esri/widgets/Home", "esri/identity/IdentityManager" ], opts)
        .then( ([Map, MapView, FeatureLayer, MapImageLayer, BasemapToggle, Expand, Legend, Search, LayerList, Graphic, GraphicsLayer, SketchViewModel, Print, PrintViewModel, Home, IdentityManager ] ) => {

      const m = new Map({
          basemap: "topo",
          highlightOptions: {
            color: [ 255, 0, 0, 1 ],
            haloOpacity: 1,
            fillOpacity: 1
          }
      });

      IdentityManager.registerToken({ server: _AGS, token: this.props.token });

      // Staging: http://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_Parcels_FS_stg/FeatureServer
      // Production: http://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_Parcels_FS/FeatureServer
      this.lineLay = new FeatureLayer({
        url: `${_AGS}/${_PAR}/FeatureServer/1`
      });
      this.pointLay = new FeatureLayer({
        url: `${_AGS}/${_PAR}/FeatureServer/2`
      });
      
      this.parcelLay = new FeatureLayer({
        url: `${_AGS}/${_PAR}/FeatureServer/0`,
        title: "Impacted Parcels",
        outFields: ["*"]
      });

      this.parcelLay.popupTemplate = {
        title: "Impacted Parcel",
        content: "Parcel ID: {PARCEL_ID}<br />{SitusAddre}",
        actions: [{
          id: "row-details", title: "Details", className: "esri-icon-link-vertical"
        }]
      };
      // m.add(this.parcelLay);
      
      // Staging map service: http://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_MapService_stg/MapServer
      // Production map service: http://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_MapService/MapServer
      const lay = new MapImageLayer({
        url: `${_AGS}/${_MAP}/MapServer`,
        title: "Engineering Data"
      });

      m.add(lay);
      lay.on("layerview-create", (evt) => { 
        const poplay = lay.sublayers.find( (l) => 'Parcels' === l.title );
        if ( poplay ) {
          poplay.popupEnabled = true;
          poplay.popupTemplate = {
            title: "Impacted Parcel",
            content: "Parcel ID: {PARCEL_ID}",
            actions: [{
              id: "row-details", title: "Details", className: "esri-icon-link-vertical"
            }]         
          }
        }
      });

      this.glay = new GraphicsLayer({
        title: 'markup',
        listMode: 'hide'
      });
      m.add(this.glay);

      new MapView({
        map: m,
        container: this.mapContainer,
        center: [ -118.30, 44.60],
        scale: 1200000,
        padding: {
          left: 10,
          top: 72
        }
      }).when( (v) => {

        this.vi = v;

        v.popup.viewModel.on("trigger-action", (evt) =>{
          const myAct = evt.action.id;
          const attr = v.popup.viewModel.selectedFeature.attributes;

          if ( myAct === "mapn-save" ) { console.log("updating notes") 
            const nelm = document.querySelector("#markupNotes") as HTMLInputElement;
            if (!nelm) return;
            attr.notes = nelm.value;

          }
          if ( myAct === "mapn-clear" ) { console.log("reseting")
            const nelm = document.querySelector("#markupNotes") as HTMLInputElement;
            if (!nelm) return;
            nelm.value = attr.notes;            
          }

          if ( myAct === "row-details" ) {
            this.props.ident(attr.PARCEL_ID);
          }
        });

        const h = new Home({ view: v });
        v.ui.add(h, "top-left");
        v.ui.move("zoom", "top-left");

        const bmg = new BasemapToggle({ view: v, nextBasemap: 'hybrid' });
        v.ui.add(bmg, "bottom-left");

        const ll = new Legend({ view: v });
        const llexp = new Expand({ view: v, content: ll, expandIconClass: "esri-icon-collection" });
        const layerlist = new LayerList({ view: v,
          listItemCreatedFunction: (evt) =>{
            const layeritem = evt.item;
            // layeritem.panel = { content: "legend", open: true}
          }
        }); 
        const exp = new Expand({ view: v, content: layerlist, expandIconClass: "esri-icon-layers" }); 


        const search = new Search({
          view: v,
          sources: [{
           featureLayer: this.parcelLay,
            searchFields: ["M_OWNER", "M_OWNER2", "PARCEL_ID"],
            displayField: "M_OWNER",
            exactMatch: false,
            outFields: ["*"],
            name: "Parcels",
            suggestionsEnabled: true,
            suggestionTemplate: "Name: {M_OWNER}, Parcel: {PARCEL_ID}",
            maxSuggestions: 6,
            placeholder: "Search by Owner or Parcel"
          },
          {
            featureLayer: new FeatureLayer({
                url: `${_AGS}/${_MAP}/MapServer/12`, // "https://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_MapService/MapServer/8",
                outFields: ["*"]
              }),
              searchFields: ["UNIQUE_ID"],
              displayField: "UNIQUE_ID",
              exactMatch: false,
              outFields: ["*"],
              name: "Communication Station",
              placeholder: "example: CS MO-01",
              maxSuggestions: 6,
              suggestionsEnabled: true,
              suggestionTemplate: "{UNIQUE_ID}"              
          },
          {
              featureLayer: new FeatureLayer({
                  url: `${_AGS}/${_MAP}/MapServer/13`, // "https://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_MapService/MapServer/9",
                  outFields: ["*"]
                }),
                searchFields: ["UNIQUE_ID"],
                displayField: "UNIQUE_ID",
                exactMatch: false,
                outFields: ["*"],
                name: "Light-Duty Fly Yard",
                placeholder: "example: LDFY UM-01",
                maxResults: 6,
                maxSuggestions: 6,
                suggestionsEnabled: true,
          },
          {
              featureLayer: {
                  url: `${_AGS}/${_MAP}/MapServer/11`, // "https://gis05s.hdrgateway.com/arcgis/rest/services/California/B2H_ROW_MapService/MapServer/11",
                  outFields: ["*"]
                },
                searchFields: ["UNIQUE_ID"],
                displayField: "UNIQUE_ID",
                exactMatch: false,
                outFields: ["*"],
                name: "Multi-Use Areas",
                placeholder: "example: MUA MO-01"
          }],
          includeDefaultSources: false
        });

        v.ui.add(search, "top-right");
        v.ui.add(exp, "top-right");
        v.ui.add(llexp, "top-right");

        /* const prt = new Print({
          view: v,
          printServiceUrl: "https://gis05s.hdrgateway.com/arcgis/rest/services/Utilities/PrintingTools/GPServer/Export%20Web%20Map%20Task"
        }); */

        this.svm = new SketchViewModel({
          view: v,
          layer: this.glay,
          pointSymbol: {
            type: 'simple-marker',
            style: 'square',
            color: 'red',
            size: '8px',
            outline: {
              color: 'red', width: 1
            }
          },
          polylineSymbol: {
            type: 'simple-line',
            color: 'red',
            width: 1,
            style: 'solid'
          },
          polygonSymbol: {
            type: 'simple-fill',
            color: 'rgba(255,0,0,0.4)',
            style: 'solid',
            outline: {
              color: "red", width: 1
            }
          }
        });

        this.svm.on("create-complete", (evt) => { console.log("sketched"); console.log(evt.geometry)
          const g = new Graphic ({
            geometry: evt.geometry.clone(),
            symbol: this.svm.graphic.symbol,
            attributes: {
              notes: "",
              logId: "",
              date: Date.now()
            },
            layer: this.glay,
            popupTemplate: this.tmplate
          });
          this.glay.add(g);
          v.popup.open({ updateLocationEnabled: true, features: [g] });
        });
        this.svm.on("update-complete", (evt) => {
          evt.graphic.geometry = evt.geometry;
          this.glay.add(evt.graphic);
          this.editGraphic=null;
        });
        this.svm.on("update-cancel", (evt) => {
          evt.graphic.geometry = evt.geometry;
          this.glay.add(evt.graphic);
          this.editGraphic=null;
        });

        /* edit markup
        v.on("click", (evt) => {
          v.hitTest(evt).then( r => {
            var results = r.results;
            if ( results.length && results[results.length -1].graphic) {
              // if ( !this.editGraphic ) {
                {
                this.editGraphic = results[results.length-1].graphic;
                if ( this.editGraphic.layer.title === 'markup') {
                  this.glay.remove(this.editGraphic);
                  this.svm.update(this.editGraphic);                    
                }
              }
            }
          })
        }); */

        const pvm = new PrintViewModel({
          view: v,
          printServiceUrl: `${_AGS}/Utilities/PrintingTools/GPServer/Export%20Web%20Map%20Task`
        });

        this.props.callback(pvm, this.glay, this.lineLay, this.pointLay, this.highlighter);
      });
    });
  }

  highlighter = ( ids: string[] ) => {

    this.vi.graphics.removeAll();

    if ( !ids || ids.length <= 0) { return; }

    const idq = ids.map( ix => `'${ix}'`).join();

    // this.vi.whenLayerView(this.parcelLay)
    //  .then( lv => {
        const q = this.parcelLay.createQuery();
        q.where = `PARCEL_ID in ( ${idq} )`;
        q.outSpatialReference = this.vi.spatialReference;
        this.parcelLay.queryFeatures(q).then( r => {
          const hi = { type: 'simple-fill', style:'none', outline: { type: 'simple-line', color: '#ff9966', width: '5px', style: 'solid' }}
          const features = r.features.map( fx => { fx.symbol = hi; return fx; });
          this.vi.graphics.addMany(features); 
          this.vi.goTo(features).then(() => {
            this.vi.zoom = this.vi.zoom - 1;
          });
        }, err => { console.log( `ouch ${err}`)} );
    //    })
  }

  _erase = () => {
    if ( this.svm) this.svm.reset();
    if ( this.glay) this.glay.removeAll();
  }

  _sketch = (evt:any) => {
    if (evt) {
      const id = evt.target.id;
      switch(evt.target.id) {
        case "polygonButton":
          this.svm.create("polygon");
          break;
        case "polylineButton":
          this.svm.create("polyline");
          break;
        default:
          this.svm.create("point");
          break;
      }
    } else {
      this.svm.create("point")
    }
  }

  render() {

    if ( ! this.props.parcelUrl ) return( <div>Loading...</div>);

    this._makeMap();

    const mapStyle = {
      width: this.props.mapWidth,
      height: "100vh"
    };


    this.props.doSketch ? this._sketch(null) : this._erase();

    return (
      <div>
        <div ref={node=> this.mapContainer = node} style={mapStyle} >
          { this.props.doSketch && 
            <div style={{ position:'absolute', top: '100px', left: '100px', zIndex: '1000' }} >
              <button className='action-button esri-icon-polyline' id='polylineButton' type='button' title='markup' onClick={this._sketch}></button>
              <button className='action-button esri-icon-map-pin' id='pointButton' type='button' title='markup' onClick={this._sketch}></button>
              <button className='action-button esri-icon-trash' id='eraseButton' type='button' title='erase' onClick={this._erase}></button>
            </div>
          }
        </div>
      </div>
    );
  }
}

export = MapArea