var React = require("react");
var LandownerScore = require('../Display/LandownerScore');

class LogInfoOverlay extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log( this.props.log );
        let list = this.props.log;

        var date = "";
        var dateToFix = list.dateAdded.split("T")[0].split("-");
        switch(dateToFix[1]) {
            case "01": date += "Jan"; break; case "02": date += "Feb"; break; case "03": date += "Mar"; break;
            case "04": date += "Apr"; break; case "05": date += "May"; break; case "06": date += "Jun"; break;
            case "07": date += "Jul"; break; case "08": date += "Aug"; break; case "09": date += "Sep"; break;
            case "10": date += "Oct"; break; case "11": date += "Nov"; break; case "12": date += "Dec"; break;
            default: break;
        }
        if( dateToFix[2].charAt[0] == "0" ) { date += " " + dateToFix[2].charAt[1]; }
        else { date += " " + dateToFix[2]; }
        date += ", " + dateToFix[0];

        return (
            <div id="asset-overview-overlay">

                <div className="panel-header">
                    <img src="assets/images/icon-contact-wh.png" alt="" aria-hidden="true" />
                    <h2>Contact Log</h2>
                    <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "edit-contact-log", null) }>edit</a>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>

                <div className="overlay-container">
                    <div className="oc-header">
                        <h3>{ list.title }</h3>
                        <span>{ date }</span>
                    </div>

                    <p><strong>Agent:</strong> { list.agentName }</p>
                    <p><strong>Contact Type:</strong> { list.contactType }</p>
                    <p><strong>Purpose of Contact:</strong> { list.phase }</p>
                    <LandownerScore score={list.score} scores={this.props.lookup} canEdit={false} doStrong={true} />
                    <p><strong>Notes:</strong> { list.notes }</p>

                    <p className="related-contacts"><strong>Related Contacts:</strong><br />
                    { list.contactIds.map(function(obj, i) {
                        let contactName = obj.contactName;
                        if( obj.contactName == " " ) {
                            contactName = this.props.parcel.owners[0].partyName;
                        }

                        return(
                            <a href="javascript:void(0);" key={obj + i} onClick={ this.props.changePanel.bind(null, "owner", this.props.parcel.owners[0].ownerId) }>{ contactName }</a>
                        )
                    }.bind(this)) }
                    </p>

                    <p className="related-contacts"><strong>Related Parcels:</strong><br />
                    { list.parcelIds.map(function(obj, i) {
                        return(
                            <a href="javascript:void(0);" key={obj + i} onClick={ this.props.changePanel.bind(null, "parcel", obj) }>{ obj }</a>
                        )
                    }.bind(this)) }
                    </p>
                </div>
            </div>
        );
    }
}

module.exports = LogInfoOverlay;
