var React = require("react");
var LinedList = require("../Display/LinedList");
var _ = require("lodash")

class ParcelPanel extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log( this.props.parcel );
        let owner = this.props.parcel.owners[0];
        let initialROEOffer = this.props.parcel.initialROEOffer.offerAmount;
        let finalROEOffer = this.props.parcel.finalROEOffer.offerAmount;
        let initialOptionOffer = this.props.parcel.initialOptionOffer.offerAmount;
        let finalOptionOffer = this.props.parcel.finalOptionOffer.offerAmount;
        let initialEasementOffer = this.props.parcel.initialEasementOffer.offerAmount;
        let finalEasementOffer = this.props.parcel.finalEasementOffer.offerAmount;

        let parcelStatus = null;
        switch( this.props.parcel.parcelStatus ) {
            case "Owner_Contacted": parcelStatus = 1; break;
            case "ROE_Obtained": parcelStatus = 2; break;
            case "Offer_Made": parcelStatus = 3; break;
            case "Easement_Signed": parcelStatus = 4; break;
            case "Compensation_Check_Cut": parcelStatus = 5; break;
            case "Documents_Recorded": parcelStatus = 6; break;
            case "Compensation_Received_by_Owner": parcelStatus = 7; break;
            default: parcelStatus = 0; break;
        }

        let ROEclass = "";
        if( parcelStatus > 1 ) {
            var cd = this.props.parcel.roeStatusCode;
            if( cd == "No_Access" ) {
                ROEclass = "checked rejected";
            } else {
                if( cd != null ) {
                    ROEclass = "checked";
                }
            }
        }

        let clog = _.orderBy(this.props.parcel.contactsLog, 'dateAdded', 'desc');

        return (
            <div id="parcel-panel">
				<div className="panel-header">
					<img src="assets/images/icon-parcel-wh.png" alt="" aria-hidden="true" />
					<h2>Parcel { this.props.parcel.parcelId }</h2>
					<span className="panel-close" onClick={ this.props.changePanel.bind(null, "stats", null) }>X</span>
				</div>
				<div id="parcel-progress">
					<ul>
						<li><span className={ (parcelStatus > 0) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Owner<br />contacted</li>

						<li><span className={ ROEclass }>
                            <img src={ (ROEclass == "checked rejected") ? "assets/images/icon-no-access.png" : "assets/images/icon-check.png" } alt="checked" /></span>ROE<br />obtained
                        </li>

						<li><span className={ (parcelStatus > 2) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Offer<br />made</li>
						<li><span className={ (parcelStatus > 3) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Easement<br />signed</li>
                        <li><span className={ (parcelStatus > 4) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Compensation<br />check cut</li>
                        <li><span className={ (parcelStatus > 5) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Document<br />recorded</li>
						<li><span className={ (parcelStatus > 6) ? "checked" : "" }><img src="assets/images/icon-check.png" alt="checked" /></span>Compensation<br />received by owner</li>
					</ul>
				</div>

				<div className="right-panel-content">
                    <a className={ (parcelStatus > 0) ? "red-button" : "hide-this" } href="javascript:void(0);" onClick={ this.props.launchOverlay.bind(null, "edit-roe-status", null) }><img src="assets/images/icon-pencil.png" alt="" aria-hidden="true" />Edit ROE Status</a>

					<h3>OWNER DETAILS</h3>
					<div className="half-column">
						<p>
							<strong>Primary:</strong> <a href="javascript:void(0);" onClick={ this.props.changePanel.bind( null, "owner", owner.ownerId ) }>{ owner.partyName }</a>
						</p>
					</div><div className="half-column">
                        <p>
                            <strong>{ (owner.contacts.length > 1) ? "Related contacts: " : "" }</strong>
                            <a href="javascript:void(0);" onClick={ this.props.launchOverlay.bind( null, "related-owners", null ) }>
                                { (owner.contacts.length > 1) ? "View " + owner.contacts.length : "" }
                            </a>
                        </p>
                    </div>

					<p><strong>Agent Logs</strong></p>
                    <LinedList
                        launchOverlay={ this.props.launchOverlay }
                        logs={ clog }
                        list={ clog } />

					<a href="javascript:void(0);" className="red-button" style={{ float: "left" }} onClick={ this.props.launchOverlay.bind(null, "add-markup", null) }><img src="assets/images/icon-pencil.png" alt="" aria-hidden="true" />Add markup</a>
					<a href="javascript:void(0);" className="red-button" onClick={ this.props.launchOverlay.bind(null, "add-contact-log", null) }><img src="assets/images/icon-add.png" alt="" aria-hidden="true" />Add log</a>

                    <h3>DOCUMENTS</h3>
                    <LinedList
                        launchOverlay={ this.props.launchOverlay }
                        documents={ this.props.parcel.documents }
                        list={ this.props.parcel.documents } />

					<a href="javascript:void(0);" className="red-button" onClick={ this.props.launchOverlay.bind(null, "add-document", null) }><img src="assets/images/icon-add.png" alt="" aria-hidden="true" />Add document</a>

                    <h3>COMPENSATION</h3>
					<div className="half-column compensation-box">
						<h4>Initial ROE Offer</h4>
						<span className={ (initialROEOffer == 0) ? "not-entered" : "" }>
                            { (initialROEOffer == 0) ? "not entered" : initialROEOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (initialROEOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "initialROE") :
                            this.props.launchOverlay.bind(null, "compensation-info", "initialROE")
                        }><img src={
                            (initialROEOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div><div className="half-column compensation-box">
						<h4>Final ROE Offer</h4>
                        <span className={ (finalROEOffer == 0) ? "not-entered" : "" }>
                            { (finalROEOffer == 0) ? "not entered" : finalROEOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (finalROEOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "finalROE") :
                            this.props.launchOverlay.bind(null, "compensation-info", "finalROE")
                        }><img src={
                            (finalROEOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div><div className="half-column compensation-box">
						<h4>Initial Option Offer</h4>
						<span className={ (initialOptionOffer == 0) ? "not-entered" : "" }>
                            { (initialOptionOffer == 0) ? "not entered" : initialOptionOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (initialOptionOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "initialOption") :
                            this.props.launchOverlay.bind(null, "compensation-info", "initialOption")
                        }><img src={
                            (initialOptionOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div><div className="half-column compensation-box">
						<h4>Final Option Offer</h4>
                        <span className={ (finalOptionOffer == 0) ? "not-entered" : "" }>
                            { (finalOptionOffer == 0) ? "not entered" : finalOptionOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (finalOptionOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "finalOption") :
                            this.props.launchOverlay.bind(null, "compensation-info", "finalOption")
                        }><img src={
                            (finalOptionOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div><div className="half-column compensation-box">
						<h4>Initial Easement Offer</h4>
						<span className={ (initialEasementOffer == 0) ? "not-entered" : "" }>
                            { (initialEasementOffer == 0) ? "not entered" : initialEasementOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (initialEasementOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "initialEasement") :
                            this.props.launchOverlay.bind(null, "compensation-info", "initialEasement")
                        }><img src={
                            (initialEasementOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div><div className="half-column compensation-box">
						<h4>Final Easement Offer</h4>
                        <span className={ (finalEasementOffer == 0) ? "not-entered" : "" }>
                            { (finalEasementOffer == 0) ? "not entered" : finalEasementOffer }
                        </span>

						<a href="javascript:void(0);" onClick={
                            (finalEasementOffer == 0) ?
                            this.props.launchOverlay.bind(null, "add-compensation", "finalEasement") :
                            this.props.launchOverlay.bind(null, "compensation-info", "finalEasement")
                        }><img src={
                            (finalEasementOffer == 0) ? "assets/images/icon-add.png" : "assets/images/icon-pencil.png"
                        } alt="" aria-hidden="true" /></a>
					</div>

				</div>
			</div>
        );
    }
}

module.exports = ParcelPanel;
