var React = require("react");

var LeftSearch = require("./LeftPanel/LeftSearch");
var LeftLegend = require("./LeftPanel/LeftLegend");
var LeftBasemap = require("./LeftPanel/LeftBasemap");

var StatsPanel = require("./RightPanel/StatsPanel");
var OwnerPanel = require("./RightPanel/OwnerPanel");
var ParcelPanel = require("./RightPanel/ParcelPanel");
var ReportsPanel = require("./RightPanel/ReportsPanel");

var RelatedOwnersOverlay = require("./Overlays/RelatedOwnersOverlay");
var LogInfoOverlay = require("./Overlays/LogInfoOverlay");
var DocInfoOverlay = require("./Overlays/DocInfoOverlay");
var AddContact = require("./Overlays/AddContact");
var AddContactLogOverlay = require("./Overlays/AddContactLogOverlay");
var AddDocumentOverlay = require("./Overlays/AddDocumentOverlay");
var CompensationInfoOverlay = require("./Overlays/CompensationInfoOverlay");
var AddCompensationOverlay = require("./Overlays/AddCompensationOverlay");
var EditROEStatus = require("./Overlays/EditROEStatus");
var MapArea = require('./MapArea');

var DataServices = require("../../Services/dataServices"); // require("../../mock/dataServices");
var dSvcs = new DataServices();
var myToken = "";

var redrawParcels = {};
var markups = {};
var markupLines = {};
var markupPoints = {};
var printViewModel = {};
var exportUrl = "";
var highlightCallback = {};

// var ownerArr;
class Application extends React.Component {

    constructor(props) {
        super(props);

        // dSvcs.getOwner("bae57ecc-3b21-4242-9a48-0d8dc979a295").then( function(d) { console.log('owner'); console.log(d); });

        this.state = {
            myMapWidth: 0,
            contactToEdit: null,
            currentOwner: null,
            currentParcel: null,
            docToShow: 0,
            latestCompType: null,
            latestCompInfo: null,
            logToShow: 0,
            menuOpened: false,
            overlayShowing: "none",
            ownerTotal: null,
            panelStatus: "closed",
            parcelDetails: null,
            parcelTotal: null,
            rightPanel: "stats",
            roeStatArray: [],
            channelArray: [],
            purposeArray: [],
            relationArray: [],
            scoreArray: [],
            agents: [],
            doSketch: false,
            waitingExport: false,
            hasExport: false,
            parcelLayerUrl: null,
            refLayerUrl: null
        }

        this.toggleMenu = this.toggleMenu.bind(this);
        this.togglePanel = this.togglePanel.bind(this);
        this.setRightPanel = this.setRightPanel.bind(this);
        this.setOverlay = this.setOverlay.bind(this);
        this.saveData = this.saveData.bind(this);
        this._parcel_details = this._parcel_details.bind(this);
    }

    componentDidMount() {

        let rightp = document.querySelector("#right-bar");
        this.setState({ myMapWidth: window.innerWidth - rightp.scrollWidth });

        var app = this;
        window.addEventListener("resize", function(evt) {
            let rightp = document.querySelector("#right-bar");
            app.setState({ myMapWidth: window.innerWidth - rightp.scrollWidth });
        });

        dSvcs.getStatistics().then( function(d) {
            var arr = new Array(6);
            arr = [ 0,0,0,0,0,0 ];
            /*
            for(var i = 0; i < d.parcelSta(tus.length; i++) {
                if(d.parcelStatus[i].title == "No_Activity") { arr[0] = d.parcelStatus[i].count; }
                if(d.parcelStatus[i].title == "ROE_Obtained") { arr[2] = d.parcelStatus[i].count; }
            }*/

            for(var i = 0; i < d.roeStatus.length; i++) {
                if(d.roeStatus[i].title == "ROE_In_Progress") { arr[1] = d.roeStatus[i].count; }
                if(d.roeStatus[i].title == "ROE_with_Conditions") { arr[3] = d.roeStatus[i].count; }
                if(d.roeStatus[i].title == "No_Access") { arr[4] = d.roeStatus[i].count; }
                if(d.roeStatus[i].title == "No_Activity") {arr[0] = d.roeStatus[i].count;}
                if(d.roeStatus[i].title == "ROE_Obtained") {arr[2] = d.roeStatus[i].count;}
                if(d.roeStatus[i].title == "Access_Granted") {arr[5] = d.roeStatus[i].count;}                
            }

            app.setState({ ownerTotal: d.numberOfOwners, parcelTotal: d.numberOfParcels, roeStatArray: arr });
        });

        // map layers
        dSvcs.getLayerDefs().then(
            function(d) {
                app.myToken = d.token;
                app.setState({
                    parcelLayerUrl: "not configured",
                    refLayerUrl: "not configured"
                })
            }
        );

        // dropdowns
        dSvcs.getLookups().then(
            function (d) {
                app.setState({ 
                    agents: d.agents, 
                    relationArray: d.relationTypes,
                    scoreArray: d.score, 
                    channelArray: d.channels, 
                    purposeArray: d.purposes });
            }
        )

        // doc type dropdown
        var _docTypes = [];
        dSvcs.getDocTypes().then( function(d){ app._docTypes=d; });        
    }

    toggleMenu() { this.setState({ menuOpened: !this.state.menuOpened }); }
    togglePanel(st) { this.setState({ panelStatus: st }); }

    setRightPanel(rpnl, newVal) {
        if(newVal != null) {
            this.setState({ overlayShowing: "none" });
            switch(rpnl) {
                case "parcel":
                    highlightCallback([ newVal ]);
                    var app = this;
                    dSvcs.getParcel( newVal ).then(function(d) {
                        app.setState({ currentParcel: newVal, parcelDetails: d, rightPanel: "parcel" });
                    });
                break;
                case "owner":
                    this.setState({ currentOwner: newVal, rightPanel: "owner" });
                break;
                case "reports":
                    const current = this.state.rightPanel;
                    const last = this.state.lastRightPanel;
                    if ( current == "reports" ) {
                        this.setState({ rightPanel: last, lastRightPanel: null });
                    } else {
                        this.setState({ rightPanel: "reports", lastRightPanel: current });
                    }
                    
                default: break;
            }
        } else {
            this.setState({ rightPanel: rpnl });
        }
    }

    setOverlay(ol, newVal) {
        if(newVal != null) {
            switch(ol) {
                case "contact-log": this.setState({ logToShow: newVal, overlayShowing: ol }); break;
                case "add-compensation": this.setState({ latestCompType: newVal, overlayShowing: ol }); break;
                case "compensation-info": this.setState({ latestCompType: newVal, overlayShowing: ol }); break;
                case "edit-contact": this.setState({ contactToEdit: newVal, overlayShowing: ol }); break;
                case "document-detail":
                    var app = this;
                    dSvcs.getDocumentInfo( newVal.documentId ).then(function(d) {
                        app.setState({ docToShow: d, overlayShowing: ol })
                    });
                break;
                default: this.setState({ overlayShowing: ol }); break;
            }
        } else {
            switch(ol) {
                case "add-contact-log": this.setState({ overlayShowing: ol, doSketch: true }); break;
                case "add-markup": this.setState({ doSketch: true }); break;
                default: this.setState({ overlayShowing: ol, doSketch: false });
            }
        }
    }

    // this is a hack to evaluate a workflow. not meant to be a long-term solution. 
    uploadAttachment(d, f, t, pcls) {
        var app = this;

        var fData = new FormData(f);
        fData.append( "documentType", "Other");
        fData.append( "documentTitle", t);
        for(var i = 0; i < pcls.length; i++) {
            fData.append("ParcelIds", pcls[i]);
        }

        var xmlHR = new XMLHttpRequest();
        xmlHR.onload = function(e) { 
            if (xmlHR.status == 200 ) { 
                console.log("attachment Ok"); 
                dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                    app.setState({ currentParcel: app.state.currentParcel, parcelDetails: d });
                });
            }
            else { console.log("Ajax error"); }
        }

        xmlHR.open("POST", dSvcs.baseUrl() + "/addDocument", true)
        xmlHR.send( fData );
    }

    uploadMarkups(myLog){

        if ( markups.graphics.length <= 0 ) return;

        markups.graphics.forEach(element => {
            element.attributes.date = Date.now(); 
            element.attributes.agent = myLog.agentName;
            element.attributes.logId = myLog.contactLogId;
        });

        var poly = markups.graphics.filter( element => element.geometry.type == "polyline");
        if ( poly.length > 0) {
            var payload = { addFeatures: poly.items };
            markupLines.applyEdits(payload).then(d => console.log(d)).catch(err => console.log(err));
        }

        var poix = markups.graphics.filter( element => element.geometry.type == "point"); 
        if ( poix.length > 0) {
            markupPoints.applyEdits({ addFeatures: poix.items }).then(d => console.log(d)).catch(err => console.log(err));
        }
    }

    saveData(type, newObj) {
        var app = this;

        if( type.indexOf("log") > -1 ) {
            var dateToFix = newObj.date.split("/");
            var date = dateToFix[2] + "-" + dateToFix[0] + "-" + dateToFix[1];
            var moreParcels = newObj.parcels.splice(1);

            if(type == "logs") {
                dSvcs.addContactLog( newObj.agent, newObj.parcels[0], moreParcels, newObj.contacts, newObj.type, newObj.title, newObj.score, newObj.notes , date , newObj.phase, exportUrl )
                .then(function(b) {
                    app.uploadMarkups(b);
                    app.setState({ doSketch: false });
                    dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                            app.setState({ currentParcel: app.state.currentParcel, parcelDetails: d });
                        });
                        exportUrl = "";
                        app.uploadAttachment(b, newObj.fileinput, newObj.title, newObj.parcels);
                    });
            } else {
                dSvcs.editContactLog( newObj.agent, newObj.parcels[0], newObj.logid, moreParcels, newObj.contacts, newObj.type, newObj.title, newObj.score, newObj.notes , date , newObj.phase ).then(function(b) {
                    dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                        app.setState({ currentParcel: app.state.currentParcel, parcelDetails: d });
                    });
                });
            }
        }

        if(type == "document-metadata") {
            console.log("IN?");
            dSvcs.editDocumentInfo( newObj.docId, newObj.trackingNo, newObj.dateSent, newObj.dateDelivered, newObj.dateReceived, newObj.dateSigned, newObj.dateRecorded, newObj.checkNo ).then(function(c) {
                console.log(c);
                dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                    console.log("HERE???");
                    app.setState({ currentParcel: app.state.currentParcel, parcelDetails: d });
                });
            })
        }

        if(type == "contact") {
            dSvcs.addContactForOwner( this.state.parcelDetails.owners[0].ownerId, newObj.firstName, newObj.lastName, newObj.address, newObj.city, newObj.state, newObj.zip, newObj.email, newObj.cellPhone, newObj.workPhone, newObj.homePhone, newObj.isPrimary, newObj.relationship ).then(function(d) {
                dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                    app.setState({ parcelDetails: d });
                });
            });
        }

        if(type == "edit-contact") {
            dSvcs.updateContactForOwner( this.state.parcelDetails.owners[0].ownerId, this.state.contactToEdit, newObj.firstName, newObj.lastName, newObj.address, newObj.city, newObj.state, newObj.zip, newObj.email, newObj.cellPhone, newObj.workPhone, newObj.homePhone, newObj.isPrimary, newObj.relationship ).then(function(d) {
                dSvcs.getParcel( app.state.currentParcel ).then(function(d) {
                    app.setState({ parcelDetails: d });
                });
            });
        }

        if(type == "initial-roe-compensation") {
            dSvcs.editInitialOffer( this.state.currentParcel, "ROE", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "final-roe-compensation") {
            dSvcs.editFinalOffer( this.state.currentParcel, "ROE", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "initial-option-compensation") {
            dSvcs.editInitialOffer( this.state.currentParcel, "Option", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "final-option-compensation") {
            dSvcs.editFinalOffer( this.state.currentParcel, "Option", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "initial-easement-compensation") {
            dSvcs.editInitialOffer( this.state.currentParcel, "Easement", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "final-easement-compensation") {
            dSvcs.editFinalOffer( this.state.currentParcel, "Easement", newObj.date, newObj.amount, newObj.notes ).then(function(d) {
                app.setState({ parcelDetails: d });
            });
        }

        if(type == "edit-roe-status") {
            dSvcs.setROEStatus( this.state.currentParcel, newObj.status ).then(function(d) {
                app.setState({ parcelDetails: d });
            });;
        }

        this.setState({ overlayShowing: "none" });
    }

    _parcel_details( pid ) {
        var app = this;
        dSvcs.getParcel( pid ).then(function(d) {
            app.setState({ currentParcel: pid, parcelDetails: d });
            app.setRightPanel("parcel", null);
        });
    }

    render() {

        let leftpanel = null;
        switch(this.state.panelStatus) {
            case "search": leftpanel = <LeftSearch />; break;
            case "legend": leftpanel = <LeftLegend />; break;
            case "basemap": leftpanel = <LeftBasemap />; break;
            default: leftpanel = ""; break;
        }

        let rightpanel = null;
        switch(this.state.rightPanel) {
            case "owner":
                rightpanel = <OwnerPanel
                    changePanel={ this.setRightPanel }
                    launchOverlay= { this.setOverlay } 
                    viewParcels={highlightCallback}
                    parcel={ this.state.parcelDetails } />; break;
            case "parcel":
                rightpanel = <ParcelPanel
                    changePanel={ this.setRightPanel }
                    launchOverlay={ this.setOverlay }
                    parcel={ this.state.parcelDetails } />; break;
            case "reports":
                rightpanel = <ReportsPanel
                    changePanel={ this.setRightPanel } />; break;
            default:
                rightpanel = <StatsPanel
                    changePanel={ this.setRightPanel }
                    ownerTotal={ this.state.ownerTotal }
                    parcelTotal={ this.state.parcelTotal }
                    roeArray={ this.state.roeStatArray } />; break;    // "stats"
        }

        let overlay = null;
        switch(this.state.overlayShowing) {
            case "related-owners":
                overlay = <RelatedOwnersOverlay
                    changePanel={ this.setRightPanel }
                    launchOverlay={ this.setOverlay }
                    parcel={ this.state.parcelDetails }
                    setOverlay={ this.setOverlay } />; break;
            case "add-contact":
                overlay = <AddContact
                    parcel={ this.state.parcelDetails } relationships={this.state.relationArray}
                    setOverlay={ this.setOverlay }
                    saveData={ this.saveData } />; break;
            case "edit-contact":
                overlay = <AddContact
                    contact={ this.state.contactToEdit }
                    parcel={ this.state.parcelDetails } relationships={this.state.relationArray}
                    setOverlay={ this.setOverlay }
                    saveData={ this.saveData } />; break;

            case "contact-log":
                overlay = <LogInfoOverlay
                    changePanel={ this.setRightPanel }
                    data={ dSvcs }
                    log={ this.state.logToShow } 
                    lookup={this.state.scoreArray}
                    panel={ this.state.rightPanel }
                    parcel={ this.state.parcelDetails }
                    setOverlay={ this.setOverlay } />; break;
            case "document-detail":
                overlay = <DocInfoOverlay
                    changePanel={ this.setRightPanel }
                    data={ dSvcs }
                    doc={ this.state.docToShow }
                    panel={ this.state.rightPanel }
                    parcel={ this.state.currentParcel }
                    setOverlay={ this.setOverlay } />; break;

            case "compensation-info":
                overlay = <CompensationInfoOverlay
                    compType={ this.state.latestCompType }
                    parcel={ this.state.currentParcel }
                    parcelDetails={ this.state.parcelDetails }
                    setOverlay={ this.setOverlay } />; break;
            case "add-compensation":
                overlay = <AddCompensationOverlay
                    compType={ this.state.latestCompType }
                    parcel={ this.state.currentParcel }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;
            case "edit-compensation":
                overlay = <AddCompensationOverlay
                    compType={ this.state.latestCompType }
                    offer={ this.state.latestCompType }
                    parcel={ this.state.parcelDetails }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;

            case "add-contact-log":
                overlay = <AddContactLogOverlay
                    owner={ this.state.currentOwner } agents = { this.state.agents }  data = { dSvcs }
                    purposes={this.state.purposeArray} channels={this.state.channelArray} scores={ this.state.scoreArray }
                    panel={ this.state.rightPanel }
                    parcel={ this.state.currentParcel }
                    parcelDetails={ this.state.parcelDetails }
                    saveData={ this.saveData }
                    snapshoot={this._snapMap.bind(this)} waitingExport={this.state.waitingExport}
                    setOverlay={ this.setOverlay } />;  break;

            case "edit-contact-log":
                overlay = <AddContactLogOverlay
                    log={ this.state.logToShow } agents = { this.state.agents }
                    purposes={this.state.purposeArray} channels={this.state.channelArray} scores={ this.state.scoreArray }
                    owner={ this.state.currentOwner }
                    panel={ this.state.rightPanel }
                    parcel={ this.state.currentParcel }
                    parcelDetails={ this.state.parcelDetails }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;

            case "add-document":
                overlay = <AddDocumentOverlay
                    data={ dSvcs }
                    docTypes = { this._docTypes }
                    parcel={ this.state.currentParcel }
                    parcelDetails={ this.state.parcelDetails }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;
            case "edit-document":
                overlay = <AddDocumentOverlay
                    data={ dSvcs }
                    docTypes = { this._docTypes }
                    parcel={ this.state.currentParcel }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;

            case "edit-roe-status":
                overlay = <EditROEStatus
                    parcel={ this.state.parcelDetails }
                    saveData={ this.saveData }
                    setOverlay={ this.setOverlay } />; break;

            default: overlay = ""; break;
        }

        return (
            <div>
                <div id="app-panel" className={ (this.state.menuOpened === true) ? "menu-opened" : "" }>
                    <div id="top-bar">
                        <a href="" target="_blank"><img src="assets/images/hdr_logo.jpg" alt="HDR" /></a>

                        <div id="breadcrumb-menu">
                            <a href="" target="_blank"><img src="assets/images/icon-home.png" alt="HDR" /></a><span>&gt;</span>
                            <h1>Landowner Communication Database</h1>
                        </div>

                        <div id="top-right">
                            <a onClick={ this.setRightPanel.bind(null, "reports")}>Reports</a>&nbsp;|&nbsp;
                            <a href="assets/IPC B2H Landowner Communication Database Training Manual.pdf" target="_blank" >Help</a>
                        </div>
                    </div>
                    <div id="body-container">
                        <MapArea ident={this._parcel_details} doSketch={this.state.doSketch} callback={this._loadedMap} 
                            mapWidth={ this.state.myMapWidth } 
                            token={ this.myToken }
                            parcelUrl={ this.state.parcelLayerUrl }
                            refUrl={ this.state.refLayerUrl }
                            />

                        {/*Hide the left panel/bar for right now: yli - 2017-10-03
                        <div id="left-bar-panel" className={ (this.state.panelStatus === "closed") ? "" : "opened" }>
                            <span className="panel-close" onClick={ this.togglePanel.bind(null, "closed") }>X</span>

                            {leftpanel}
                        </div>
                        <div id="left-bar">
                            <a href="javascript:void(0);"
                                className={ (this.state.panelStatus === "search") ? "selected" : "" }
                                onClick={ this.togglePanel.bind(null, "search") }>
                                    <img src="assets/images/icon-search.png" alt="" aria-hidden="true" />
                                    <span>Search</span>
                            </a>
                            <a href="javascript:void(0);"
                                className={ (this.state.panelStatus === "legend") ? "selected" : "" }
                                onClick={ this.togglePanel.bind(null, "legend") }>
                                    <img src="assets/images/icon-legend.png" alt="" aria-hidden="true" />
                                    <span>Legend</span>
                            </a>
                            <a href="javascript:void(0);"
                                className={ (this.state.panelStatus === "basemap") ? "selected" : "" }
                                onClick={ this.togglePanel.bind(null, "basemap") }>
                                    <img src="assets/images/icon-basemap.png" alt="" aria-hidden="true" />
                                    <span>Basemap</span>
                            </a>
                        </div>
                        */}

                        {/* <MapArea />

                        <div id="map-area">
                        </div> */}
                        <div id="right-bar">
                            {rightpanel}
                        </div>
                    </div>

                    <div id="menu-panel">
                        <div className="panel-header">
                            <h2>MENU</h2>
                        </div>
                        <ul>
                            <li><a href="">Item 1</a></li>
                            <li><a href="">Item 2</a></li>
                            <li><a href="">Item 3</a></li>
                        </ul>

                        <div id="menu-panel-bottom">
                            <a href="">
                                <img src="assets/images/icon-account.png" alt="HDR" /><span>My account</span>
                            </a><a href="" className="logout-btn">
                                <span>Logout</span>
                            </a>
                        </div>
                    </div>
                </div>

                <div id="overlay-screen" className={ this.state.overlayShowing !== "none" ? "showing" : "" }>
                    <span onClick={ this.setOverlay.bind(null, "none", null) }></span>
                    {overlay}
                </div>
            </div>
        );
    }

    _loadedMap( printvm, graphicsLay, lineLay, pointLay, highlighter, redraw ) {
        printViewModel = printvm;
        markups = graphicsLay;
        markupLines = lineLay;
        markupPoints = pointLay;
        highlightCallback = highlighter;
        redrawParcels = redraw;
    }

    _snapMap() {
        this.setState({ waitingExport: true });
        printViewModel.print({ format: "pdf", layout: "map_only", exportOptions: { width: 800, height: 1100, dpi: 96 } })
            .then(d => { console.log(d.url); exportUrl = d.url; this.setState({ waitingExport: false }); }, e => { alert(e.message); this.setState({ waitingExport: false }); });
        // not sure which promise Esri is using...
        // .when( function(d) {
        //        console.log(d);
        //    });
    }
}

module.exports = Application;
